const baseDoc = () => ({
  _id: "",
  code: "",
  type: "",
  value: "",
  currency: "all",
  validity: "",
  validityRange: {
    start: new Date(),
    end: new Date(),
  },
  category: "",
  log: [],
  text: "",
  description: "",
  status: false,
});
export default {
  props: {
    model: String,
  },
  data() {
    return {
      editMode: false,
      doc: baseDoc(),
      couponTypes: [
        {
          name: "Select Type",
          value: null,
        },
        {
          name: "Percentage",
          value: "percentage",
        },
        {
          name: "Cash Discount",
          value: "direct-discount",
        },
      ],
      categoryTypes: [
        {
          name: "Select Type",
          value: null,
        },
        {
          name: "General",
          value: "general",
        },
        {
          name: "Personal",
          value: "personal",
        },
      ],
      currencies: [
        {
          name: "Select Currency",
          value: null,
        },
      ],
      loading: false,
      updated: false,
    };
  },
  mounted() {
    this.fetchActiveCurrencies();
  },
  methods: {
    setText() {
      this.doc.text = this.getCouponTextTemplate();
    },
    getCouponTextTemplate() {
      const coupon = this.doc;

      /* check if coupon is expired or not */
      if (new Date(coupon.validityRange.end).getTime() < new Date().getTime())
        this.couponExpired = true;

      const unit = coupon.type === "percentage" ? "%" : coupon.currency;
      return `Use coupon code ${coupon.code} and get flat ${
        coupon.value
      } ${unit} off on your order. Offer valid from ${this.$formatDate(
        coupon.validityRange.start,
        true
      )} - ${this.$formatDate(coupon.validityRange.end, true)}. Hurry!`;
    },
    async fetchActiveCurrencies() {
      const request = await this.$post("/findDocuments", {
        model: "currency",
        filters: {
          adminEnabled: true,
          status: true,
        },
      });

      if (request.resolved == false) {
        return;
      }

      const currencies = request.response;

      this.currencies = currencies.map((c) => ({
        name: c.name,
        value: c.code,
      }));
      this.currencies.unshift(
        { name: "All Currencies", value: "all" },
        { name: "Indian Rupee", value: "INR" }
      );
    },
    async updateDocument() {
      this.doc.code = this.doc.code.toUpperCase();

      this.loading = true;
      const result = await this.$updateDocument(
        this.model,
        this.doc,
        this.editMode
      );
      this.loading = false;

      if (!result.updated) return;

      this.$emit("updated");
      this.populateForm(result.doc);
      this.$flash(this);
    },
    async deleteDocument() {
      this.loading = true;
      const result = await this.$deleteDocument(this.model, this.doc._id);
      this.loading = false;

      if (!result.deleted) return;

      this.$emit("updated");
      this.resetForm();
      this.$flash(this);
    },
    populateForm(details) {
      const {
        _id,
        code,
        type,
        value,
        currency,
        validity,
        validityRange,
        log,
        category,
        text,
        description,
        status,
      } = details;
      this.doc = {
        _id,
        code,
        type,
        value: value.toString(),
        currency,
        validity: validity.toString(),
        validityRange,
        log,
        category,
        text,
        description,
        status,
      };
      this.editMode = true;
    },
    closeForm() {
      this.resetForm();
      this.$emit("close");
    },
    resetForm() {
      this.populateForm(baseDoc());
      this.editMode = false;
    },
  },
};
